/* Body container */
.ppostcard-container {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(to bottom, #fffbe6 0%, #ffe8b3 100%);
  margin-top: 50px;
  margin-left: 330px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* Postcard box layout */
.ppostcard-container {
  width: 620px;
  height: 400px;
  background: #fff;
  border: 2px solid #f57ea4;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  display: flex;
  padding: 25px 30px;
  position: relative;
  box-sizing: border-box;
}

/* Left side - Message */
.ppostcard-left {
  flex: 1;
  padding-right: 25px;
  border-right: 2px solid #f57ea4;
  position: relative;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* Textarea styling */
.ppostcard-left textarea {
  width: 100%;
  height: 270px;
  resize: none;
  border: none;
  padding-bottom: 10px;
  font-size: 16px;
  background: transparent;
  color: #333;
  outline: none;
  font-family: inherit;
}

/* Right side */
.ppostcard-right {
  flex: 1;
  padding-left: 25px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 15px;
  font-size: 14px;
  color: #555;
  position: relative;
}

/* Label for inputs */
.ppostcard-right label {
  font-weight: 600;
  margin-bottom: 5px;
  display: block;
  color: #f57ea4;
  font-size: 14px;
}

/* Input fields and select dropdown */
.ppostcard-right input,
.ppostcard-right select {
  border: none;
  border-bottom: 2px solid #f57ea4;
  padding: 8px 6px;
  font-size: 14px;
  width: 100%;
  background: transparent;
  color: #333;
  outline: none;
  font-family: inherit;
  box-sizing: border-box;
}

/* Smaller urgency select */
.ppostcard-right select {
  width: 50%;
  font-size: 13px;
  padding: 6px 8px;
}

/* Checkbox container for label and box side by side */
.checkbox-container {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: -5px; /* align better with inputs */
}

/* Checkbox styling */
.ppostcard-right input[type="checkbox"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
}

/* Checkbox label */
.checkbox-container label {
  margin: 0;
  font-size: 14px;
  color: #333;
  cursor: pointer;
}

/* Submit button */
.ppostcard-submit button {
  padding: 10px 24px;
  background-color: #f57ea4;
  color: #fff;
  border: none;
  cursor: pointer;
  margin-top: 15px;
  font-weight: bold;
  border-radius: 4px;
  transition: background-color 0.3s ease;
  align-self: flex-start;
  font-size: 15px;
}

.ppostcard-submit button:hover {
  background-color: #e2548e;
}

/* Error text */
.ppostcard-submit p {
  color: red;
  margin-top: 10px;
}

/* Extend the vertical dividing line */
.ppostcard-left {
  border-right-width: 2.5px;
}

/* Add subtle horizontal lines for dividing inputs on right */
.ppostcard-right input,
.ppostcard-right select {
  border-bottom-width: 2.5px;
}

/* Fix the "to:" label - remove or hide */
.to-label {
  display: none;
}
