.leaderboard-container {
  padding: 8rem;
  background: linear-gradient(to bottom, #fffbe6 0%, #ffe8b3 100%);
  color: #d15d91;
  text-align: center;
  font-family: 'Comic Sans MS', sans-serif;
}

.leaderboard-list {
  list-style: none;
  padding: 0;
  max-width: 400px;
  margin: 2rem auto;
}

.leaderboard-list li {
  background: linear-gradient(to bottom, #fff7d2 0%, #fada90 100%);
  border: 2px solid #f8a8c9;
  padding: 1rem;
  margin: 0.5rem 0;
  border-radius: 12px;
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  font-size: 1.1rem;
}

.rank {
  color: #ff69b4;
}

.name {
  flex-grow: 1;
  text-align: center;
}

.count {
  color: #999;
}
