/* Base styles */
body, .home-container {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: 'Georgia', serif;
  background: linear-gradient(to bottom, #fffbe6 0%, #ffe8b3 100%);
  color: #333;
  overflow-x: hidden;
  position: relative;
}

/* Sparkle overlay */
body::after {
  content: '';
  position: fixed;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background: radial-gradient(#fffae0 1px, transparent 1px),
              radial-gradient(#fff3cc 1px, transparent 1px);
  background-size: 4px 4px, 8px 8px;
  background-position: 0 0, 10px 10px;
  opacity: 0.06;
  pointer-events: none;
  z-index: 0;
  animation: sparkle 80s linear infinite;
}

@keyframes sparkle {
  from { transform: translate(0, 0); }
  to { transform: translate(-100px, 100px); }
}

/* Container styling */
.home-container {
  position: relative;
  z-index: 1;
  padding: 2.5rem 1rem;
  text-align: center;
}

/* Title style */
.home-title {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  color: #d97706;
  text-shadow: 0 0 6px rgba(255, 196, 0, 0.2), 0 0 12px rgba(255, 200, 80, 0.2);
  font-weight: bold;
}

/* Card base style */
.wish-card {
  border: 1.5px solid #ffe6a7;
  border-radius: 16px;
  box-shadow: 0 4px 12px rgba(255, 207, 115, 0.2);
  margin: 1.5rem 0;
  padding: 1.8rem;
  max-width: 600px;
  color: #553e16;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
  overflow: hidden;
  animation: floatUp 5s ease-in-out infinite alternate;
  background: #fffaf0;
}

/* Alternate alignments */
.wish-card:nth-child(odd) {
  margin-left: auto;
  margin-right: 5%;
  text-align: left;
}
.wish-card:nth-child(even) {
  margin-right: auto;
  margin-left: 5%;
  text-align: right;
}

@keyframes floatUp {
  0% { transform: translateY(0); }
  100% { transform: translateY(-10px); }
}

.wish-card::before {
  content: '';
  position: absolute;
  top: -10%; left: -10%;
  width: 120%; height: 120%;
  background: radial-gradient(circle, rgba(255, 248, 220, 0.08) 0%, transparent 70%);
  animation: floatGlow 7s ease-in-out infinite alternate;
  z-index: 0;
}

@keyframes floatGlow {
  0% { transform: translateY(0); }
  100% { transform: translateY(-4px); }
}

.wish-card:hover {
  transform: scale(1.02);
  box-shadow: 0 6px 18px rgba(255, 210, 120, 0.4);
}

/* Sticker emoji */
.sticker {
  font-size: 2rem;
  margin-bottom: 0.5rem;
  z-index: 1;
  position: relative;
}

/* Content inside cards */
.wish-header {
  font-weight: bold;
  font-size: 1.2rem;
  color: #b45309;
  margin-bottom: 0.4rem;
  position: relative;
  z-index: 1;
}

.wish-text {
  font-style: italic;
  color: #6b4e1f;
  font-size: 1.05rem;
  margin: 0.8rem 0;
  z-index: 1;
  position: relative;
}

.wish-meta, .status-text, .wish-category-location {
  font-size: 0.92rem;
  color: #8c6d3f;
  z-index: 1;
  position: relative;
}

/* Button styling */
.wish-button {
  background: linear-gradient(to right, #fcd34d, #fbbf24);
  border: none;
  padding: 0.6rem 1.5rem;
  border-radius: 40px;
  font-weight: bold;
  color: #3b2600;
  cursor: pointer;
  font-size: 1rem;
  box-shadow: 0 0 8px rgba(255, 204, 0, 0.2);
  transition: all 0.3s ease;
  position: relative;
  z-index: 1;
  margin-top: 1rem;
}

.wish-button:hover {
  background: linear-gradient(to right, #fef3c7, #fde68a);
  transform: scale(1.04);
  box-shadow: 0 0 10px rgba(255, 223, 109, 0.5);
}

.search-container {
  position: relative;
  width: 90%;
  max-width: 500px;
  margin: 1.5rem auto;
}

.cute-search-bar {
  width: 100%;
  padding: 0.8rem 1rem 0.8rem 3rem;
  border: none;
  border-radius: 999px;
  background: #f0f4ff;
  box-shadow: 5px 5px 15px rgba(180, 180, 255, 0.4);
  font-size: 1rem;
  color: #555;
  transition: all 0.3s ease;
}

.cute-search-bar::placeholder {
  color: #aaa;
}

.cute-search-bar:focus {
  outline: none;
  box-shadow: 0 0 10px #b8c6ff;
}

.search-icon {
  position: absolute;
  top: 50%;
  left: 1rem;
  transform: translateY(-50%);
  font-size: 1.2rem;
  color: #888;
}

.clickable-username {
  color: #d15d91;
  cursor: pointer;
  text-decoration: underline;
  font-weight: bold;
}

.clickable-username:hover {
  color: #c2185b;
}
.like-dislike-container {
  margin-top: 10px;
  display: flex;
  gap: 12px;
  position: relative; /* Important: ensures the container stacks correctly */
  z-index: 2;          /* Keeps it above decorative/background elements */
}

.like-btn,
.dislike-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 14px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  border: none;
  transition: background-color 0.3s ease;
  position: relative; /* Important: ensures it's above lower layers */
  z-index: 2;          /* Lifts the button above anything overlapping */
}

.like-btn {
  background-color: #d0f0d0; /* pastel green */
  color: #256029;
}

.like-btn:hover {
  background-color: #b4e7b4;
}

.dislike-btn {
  background-color: #f9d2d2; /* pastel red */
  color: #8a1c1c;
}

.dislike-btn:hover {
  background-color: #f4b6b6;
}

/* Optional: visual cue if already voted */
.like-btn.voted,
.dislike-btn.voted {
  opacity: 0.7;
  pointer-events: none; /* Prevents further clicking */
}
