/* src/ChatScreen.css */

.chat-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-width: 100%;
  background: linear-gradient(to bottom, #fffbe6 0%, #ffe8b3 100%);
  font-family: 'Segoe UI', sans-serif;
  padding-top: 50px;
}

.chat-box {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  background: linear-gradient(to bottom, #fffbe6 0%, #ffe8b3 100%);
  border-radius: 10px;
  margin-bottom: 10px;
  max-width: 700px;
  width: 100%;
  align-self: center;
  scroll-behavior: smooth;
}

.chat-message {
  max-width: 70%;
  margin-bottom: 15px;
  padding: 10px 14px;
  border-radius: 15px;
  position: relative;
  word-wrap: break-word;
  box-shadow: 0 1px 2px rgba(0,0,0,0.1);
}

.chat-message.own {
  background-color: #fff3cd;
  margin-left: auto;
  text-align: right;
}

.chat-message.other {
  background-color: #d9eaf7;
  margin-right: auto;
  text-align: left;
}

.chat-text {
  font-size: 1rem;
}

.chat-time {
  font-size: 0.75rem;
  margin-top: 4px;
  color: gray;
}

.chat-input-form {
  display: flex;
  padding: 10px 15px;
  background: #fff;
  border-top: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 0 -2px 5px rgba(0,0,0,0.03);
  max-width: 700px;
  align-self: center;
  width: 100%;
  margin-bottom: 60px;
}

.chat-input {
  flex: 1;
  padding: 10px 15px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 20px;
  outline: none;
}

.chat-send-button {
  background-color: #ffb300;
  border: none;
  color: white;
  padding: 10px 18px;
  margin-left: 10px;
  border-radius: 20px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.chat-send-button:hover {
  background-color: #ffa000;
}

.chat-image {
  max-width: 200px;
  border-radius: 8px;
  margin: 5px 0;
}

.chat-icon-button {
  background: transparent;
  border: none;
  font-size: 20px;
  color: #555;
  padding: 0 10px;
  cursor: pointer;
}

.chat-icon-button:hover {
  color: #000;
}

.chat-audio {
  margin-top: 4px;
  width: 100%;
  max-width: 200px;
}
