/* Login.css */
.body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(to bottom, #fffbe6 0%, #ffe8b3 100%);
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70vh;
}

.lgpostcard {
  width: 400px;
  padding: 30px 40px;
  background: #fff;
  border: 2px solid #f57ea4;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0px auto;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.lgpostcard h2 {
  color: #333;
  margin-bottom: 25px;
  font-weight: bold;
}

.lgpostcard form {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.lgpostcard input {
  border: none;
  border-bottom: 2px solid #f57ea4;
  padding: 10px;
  margin-bottom: 20px;
  font-size: 16px;
  width: 100%;
  background: transparent;
  color: #333;
  outline: none;
  transition: border-color 0.3s ease;
}

/* Remove autofill background */
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px white inset !important;
  -webkit-text-fill-color: #333 !important;
}

.lgpostcard input:focus {
  border-color: #e2548e;
}

.lgpostcard button {
  padding: 12px 25px;
  background-color: #f57ea4;
  color: #fff;
  border: none;
  cursor: pointer;
  font-weight: bold;
  border-radius: 6px;
  transition: background-color 0.3s ease;
  margin-right: 15px;
  min-width: 110px;
}

.lgpostcard button:hover:not(:disabled) {
  background-color: #e2548e;
}

.lgpostcard button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.lgpostcard form > div.buttons {
  display: flex;
  margin-top: 10px;
}

.lgpostcard p.error {
  color: red;
  margin-top: 15px;
  font-weight: 600;
}

/* Align Register button properly */
.lgpostcard .register-btn {
  background-color: #f57ea4;
  border-radius: 6px;
  min-width: 110px;
  font-weight: bold;
  margin-left: 15px;
  transition: background-color 0.3s ease;
}

.lgpostcard .register-btn:hover {
  background-color: #e2548e;
}
