/* src/ChatPage.css */

.chat-page-container {
  display: flex;
  height: 100vh;
  background: linear-gradient(to bottom, #fffbe6 0%, #ffe8b3 100%);
  font-family: 'Segoe UI', sans-serif;
}

.chat-list {
  width: 28%;
  border-right: 1px solid #ddd;
  overflow-y: auto;
  background-color: #fff5f2;
  padding-right: 0.5rem;
  padding-bottom: 40px;
}

.chat-conversation {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 0.5rem 1rem;
  background-color: #fffaf5;
}

.chat-placeholder {
  margin: auto;
  font-size: 1.2rem;
  color: #999;
}
