/* General postcard layout */


.body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(to bottom, #fffbe6 0%, #ffe8b3 100%);
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Postcard container */
.rpostcard {
  margin-top: 100px;
  width: 600px;
  height: 300px;
  background: #fff;
  border: 2px solid #f57ea4;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  display: flex;
  padding: 20px;
  position: relative;
}

/* Left and right halves */
.rpostcard-left,
.rpostcard-right {
  flex: 1;
  padding: 20px;
}

/* Left section for illustration or "From" */
.rpostcard-left {
  border-right: 2px solid #f57ea4;
  background-size: contain;
  position: relative;
}

/* From label */
.from-label {
  position: absolute;
  bottom: 20px;
  left: 30px;
  color: #f57ea4;
  font-weight: bold;
}

/* Right section for form */
.rpostcard-right {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Input fields */
input {
  border: none;
  border-bottom: 2px solid #f57ea4;
  padding: 10px;
  margin-bottom: 15px;
  font-size: 16px;
  width: 100%;
  background: transparent;
  color: #333;
}

/* Buttons */
button {
  padding: 10px 20px;
  background-color: #f57ea4;
  color: #fff;
  border: none;
  cursor: pointer;
  margin-top: 10px;
  font-weight: bold;
  border-radius: 4px;
}

button:hover {
  background-color: #e2548e;
}

/* Error message */
.error {
  color: red;
  margin-top: 10px;
}

/* Stamp area */
.rstamp {
  position: absolute;
  top: 20px;
  right: 30px;
  width: 60px;
  height: 80px;
  border: 2px solid #f57ea4;
}


